@keyframes move {
  from {
    transform: translateX(-50%)rotate(0);
  }

  to {
    transform: translateX(-50%)rotate(360deg);
  }
}

.page__body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__seconds {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite backwards move;
  position: absolute;
  top: 2vmin;
  left: 50%;
  transform: translateX(-50%);
}

.stopwatch__minutes {
  transform-origin: bottom;
  z-index: -1;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite backwards move;
  position: absolute;
  top: 20vmin;
  left: 50%;
  transform: translateX(-50%);
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch--speed-up .stopwatch__seconds {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__minutes {
  animation-duration: 600s;
}
